/*        */
h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
}

/*        (ul) */
ul {
    list-style: none;
    padding-left: 0;
    margin: 0;
}

/*        */
li {
    text-decoration: none;
    color: inherit;
}

.address {
    font-style: normal;
}


/*          */ 

body {
    margin: 0;
}

a {
    text-decoration: none;
}

ul,
li {
    list-style-type: none;
    margin: 0;
}


